}

main.container {
  flex-direction: column;
}

header {
  display: flex;
  height: 70px;
  box-shadow: 0px 3px 6px #00000029;
  width: 100%;
  position: relative;
}

.logo {
  text-decoration: none;
  font-size: 32px;
  font-family: "Raleway", sans-serif;
  color: #08a6e4;
  text-align: left;
  width: 142px;
  height: 37px;
  align-self: center;
  margin: 20px;
  letter-spacing: 0px;
}

.page-nav {
  display: flex;
  align-items: center;
}

.page-nav-list {
  display: flex;
}

.page-nav .page-nav-list li a {
  text-decoration: none;
  height: 15px;
  font-size: 11px;
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  color: grey;
  padding: 20px 0;
  margin: 0 42.5px;
  letter-spacing: 3.28px;
}

.page-nav .page-nav-list li a:hover {
  color: black;
  border-bottom: 4px solid #08a6e4;
}

#menu {
  display: none;
}

@media (max-width: 840px) {
  .page-nav {
    position: relative;
    transform: translateY(-400px);
    transition: transform 0.3s 0.1s ease-in-out;
  }
  .page-nav .page-nav-list {
    display: flex;
    flex-direction: column;
    border: 1px solid #ccc;
    position: absolute;
    right: 0;
    top: 72px;
  }
  .page-nav .page-nav-list li {
    height: 44px;
    display: flex;
    align-items: center;
    border: 1px solid #eee;
    background-color: white;
  }
  .page-nav .page-nav-list li a:hover {
    color: black;
    border-bottom: none;
  }

  #menu {
    visibility: hidden;
  }

  .menu-label,
.menu-label::before,
.menu-label::after {
    position: absolute;
    width: 40px;
    height: 2px;
    border-radius: 5px;
    background-color: #08a6e4;
    transition: 0.6s;
  }

  .menu-label {
    cursor: pointer;
    top: 50%;
    right: 5%;
    margin-left: -50px;
    margin-top: -7.5px;
  }

  .menu-label::before {
    content: "";
    transform: translateY(-15px);
  }

  .menu-label::after {
    content: "";
    transform: translateY(15px);
  }

  #menu:checked + .menu-label {
    width: 40px;
  }

  #menu:checked + .menu-label::before {
    transform: rotate(45deg) translate(0);
  }

  #menu:checked + .menu-label::after {
    transform: rotate(-45deg) translate(0);
  }

  #menu:checked ~ .page-nav {
    transform: translateX(0);
  }
}
