  text-align: left;
}

.left-side-form h2 {
  font-family: var(--fontSans);
  font-size: 16px;
  color: var(--black);
  text-align: left;
}

.form-icons {
  display: flex;
  flex-direction: row;
}

.form-icons .info-email,
.form-icons .telephone-number {
  font-size: 16px;
  font-family: var(--fontSans);
  font-size: 12px;
  color: #95989a;
}

.left-side-form {
  width: 50%;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-evenly;
}

form {
  width: 50%;
}

.main-form {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.main-form input {
  margin-bottom: 48px;
}

.main-form .name-form,
.main-form .email-form {
  font-family: var(--fontSans);
  font-size: 11px;
  color: var(--mainColor);
  font-weight: bold;
  letter-spacing: 3.28px;
}

input {
  border-top: none;
  border-right: none;
  border-left: none;
  border-bottom: 1px solid #95989a;
  margin-top: 24px;
}

.checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkbox {
  cursor: pointer;
  display: flex;
}

/* 
  .greencheckbox {
    position: absolute;
  }
    */
.checkbox span {
  width: 24px;
  height: 24px;
  flex-shrink: 0;
  border: 1px solid #95989a;
  cursor: pointer;
  border-radius: 4px;
  position: relative;
}

p {
  font-family: var(--fontSans);
  font-size: 12px;
  color: #95989a;
}

.checkbox input:checked + span {
  background-image: url("../assets/Checkmark.svg");
  background-repeat: no-repeat;
  background-position: center;
}

/* button niebieski */
.send-button {
  display: inline-block;
  border-radius: 35px;
  box-shadow: 5px 10px 14px #07ace629;
  color: var(--white);
  font-family: var(--fontSans);
  font-weight: bold;
  text-align: center;
  padding: 22px 101px;
  font-size: 13px;
  /* width: 226px;
    height: 57px; */
  box-sizing: border-box;
  text-decoration: none;
  width: fit-content;
  margin-top: 32px;
  align-self: flex-end;
}

