header {
  display: flex;
  height: 70px;
  box-shadow: 0px 3px 6px #00000029;
  width: 100%;
  position: relative;
}

.logo {
  text-decoration: none;
  font-size: 32px;
  font-family: "Raleway", sans-serif;
  color: #08a6e4;
  text-align: left;
  width: 142px;
  height: 37px;
  align-self: center;
  margin: 20px;
  letter-spacing: 0px;
}

.page-nav {
  display: flex;
  align-items: center;
}

.page-nav-list {
  display: flex;
}

.page-nav .page-nav-list li a {
  text-decoration: none;
  height: 15px;
  font-size: 11px;
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  color: grey;
  padding: 20px 0;
  margin: 0 42.5px;
  letter-spacing: 3.28px;
}

.page-nav .page-nav-list li a:hover {
  color: black;
  border-bottom: 4px solid #08a6e4;
}

#menu {
  display: none;
}